import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController} from 'ionic-angular';

import { Geolocation } from '@ionic-native/geolocation';

import { FirebaseListObservable } from 'angularfire2/database';
import { DataService } from '../../providers/data.service';

declare var google;

@IonicPage()
@Component({
  selector: 'page-info-map',
  templateUrl: 'info-map.html',
})
export class InfoMapPage {

  map: any;
  directionsService: any = null;
  directionsDisplay: any = null;
  bounds: any = null;
  myLatLng: any;
  waypoints: any[];

  datos: FirebaseListObservable<any>;

  constructor(
    public navCtrl: NavController,
    public geolocation: Geolocation,
    private dataService: DataService,
    public loadCtrl: LoadingController
    ) {

      this.directionsService = new google.maps.DirectionsService();
        this.directionsDisplay = new google.maps.DirectionsRenderer();
        this.bounds = new google.maps.LatLngBounds();

          this.waypoints = [
            {
              location: { lat: 4.6241329, lng: -74.1768411 },
              stopover: true,
            },
            {
              location: { lat: 4.6247745, lng: -74.1698888 },
              stopover: true,
            },
            {
              location: { lat: 4.6212241, lng: -74.1631081 },
              stopover: true,
            },
            {
              location: { lat: 4.6222508, lng: -74.1667989 },
              stopover: true,
            }
          ];
  }

  ionViewDidLoad() {

    let load = this.loadCtrl.create({
      content: 'Cargando...'
    });
    load.present();
    this.datos = this.dataService.getAll();
    this.datos.subscribe(data=>{
      load.dismiss(); 
      var latitude = data[0].latitude; 
      var longitude = data [0].longitude;
      var myLatLng = new google.maps.LatLng(latitude,longitude);
      console.log(myLatLng);

      var map = new google.maps.Map(document.getElementById('map'),{
        center:  {lat: latitude, lng: longitude}, //mi posicion
        zoom: 10,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
      });

      var marker = new google.maps.Marker({
        map: map,
        position: myLatLng,
        animation: google.maps.Animation.DROP,
        icon: 'http://chart.apis.google.com/chart?chst=d_map_pin_icon&chld=bus|FFFF00',
        title: "Preventa 1",
        });
      })
  
    }

}



